let resizeFunc = (e) => {
  let pageDimen = {
    height: window.innerHeight,
    width: window.innerWidth
  };
  let backgroundEle = document.getElementById("dynbackground");
  if (pageDimen.height > pageDimen.width) {
    backgroundEle.classList.add("portrait");
  } else {
    backgroundEle.classList.remove("portrait");
  }
  let offset = (pageDimen.width - backgroundEle.width) / 2;
  backgroundEle.style.left = Math.abs(offset) * -1 + "px";
}

window.addEventListener("resize", resizeFunc)

resizeFunc();
